import { MainAreaWidget } from '@jupyterlab/apputils';
import { ILauncher } from '@jupyterlab/launcher';
import { CounterWidget } from './widget';
// import { reactIcon } from '@jupyterlab/ui-components';
import { LabIcon } from '@jupyterlab/ui-components';
const CHAT_ICON = '<svg xmlns="http://www.w3.org/2000/svg" xmlns:xlink="http://www.w3.org/1999/xlink"><image xlink:href="data:;base64," width="52" height="52" /></svg>';
export const chatIcon = new LabIcon({
    name: 'jitsi:chat',
    svgstr: CHAT_ICON
});
/**
 * The command IDs used by the react-widget plugin.
 */
var CommandIDs;
(function (CommandIDs) {
    CommandIDs.create = 'create-react-widget';
})(CommandIDs || (CommandIDs = {}));
/**
 * Initialization data for the react-widget extension.
 */
const extension = {
    id: 'react-widget',
    autoStart: true,
    optional: [ILauncher],
    activate: (app, launcher) => {
        const { commands } = app;
        const command = CommandIDs.create;
        commands.addCommand(command, {
            caption: 'tgCloud Cloud platform',
            label: 'TigerGraph Cloud',
            icon: args => (args['isPalette'] ? null : chatIcon),
            execute: () => {
                const content = new CounterWidget();
                const widget = new MainAreaWidget({ content });
                widget.title.label = 'TigerGraph Cloud';
                widget.title.icon = chatIcon;
                app.shell.add(widget, 'main');
            }
        });
        if (launcher) {
            launcher.add({
                command
            });
        }
    }
};
export default extension;
